.carouselDiv {
  width: 70vw;
  margin: auto;
  box-shadow: 15px 15px 40px 0 rgba(0, 0, 0, 0.2);
}

@media (min-width: 280px) and (max-width: 767px) {
  .oneProjectImage {
    height: 50vh;
  }

  .carouselDiv {
    width: 90vw;
  }
}


@media (min-width: 280px) and (max-width: 320px) {
  .oneProjectImage {
    height: 35vh;
  }

  .carouselDiv {
    width: 85vw;
  }
}
